
@import "utilities/variables_header_footer";

@media all {
	#contact-us-section {
		background-color: $cep;
			
		color: $white;
		margin: 0 auto;			
		padding: 40px 10%;
		#toggle-contact-form {
				cursor: pointer;
			}
		ul {
			margin-bottom: 0px;
		}
		li, a {
			color: $white;
			font-size: 15px;
			letter-spacing: .5px;
			margin: 0px;
			padding: 0px;
			text-decoration: none;
		}
		.title {
			font-size: 20px;
			margin: 0px;
			padding: 0px;
		}
		img {
			height: 26px;
			margin-right: 10px;
			vertical-align: text-bottom;
		}
		span {
			margin-left: 10px;
			vertical-align: text-top;
		}
	}
		#contact-form-section {
			background-color: $white;
			color: $black;
			display: none;
			margin: 0 auto;
			padding: 30px 10%;
			.row {
				margin-bottom: 50px;
				&.thank-you {
					margin-bottom: 0px;
				}
			}
            .row:nth-last-child(3) {
              margin-bottom: 25px;
			}
            .row:nth-last-child(2), .row:last-child {
              margin-bottom: 15px;
            }
			.title {
				font-size: 25px;
			}
			input[type=text], select {
				background-color: transparent;
				border: 0px;
				border-bottom: 1px solid $light-grey;
				border-radius: 0px;
				outline: none;
				padding-bottom: 10px;
				width: 100%;
				-webkit-appearance: none;
				-webkit-box-shadow: 0 0 0px 1000px $white inset;
			}
			textarea, textarea:focus {
				border: 1px solid $light-grey;
				height: 200px;
				outline: none;
				padding: 15px;
				resize: none;
				width: 100%;
			}
			.input-error {
				border-bottom: 1px solid $error;
			}
			.contact-error {
				color: $error;
				margin: 5px 0px 25px 0px;
			}
		}
		#links-section {
			background-color: $darkerer-grey;
			color: $dark-grey;
			margin: 0px auto;
			padding: 50px 10% 25px 10%;
			hr {
				background-image: url("/images/slash.svg");
				background-repeat: repeat-x;
				background-size: 10px 7px;
				border: 0px;
				height: 7px;
			}
			a {
				color: inherit;
				text-decoration: none;
			}
			a:hover {
				color: $white;
			}
			.links {
				margin-bottom: 70px;
				.col-sm-12 {
					padding-bottom: 5px;
				}
				.collapse {
					display: block;
					padding-top: 5px;
				}
				a {
					font-size: 13px;
					&.logo:after {
						content: url("/images/logo-dnb-white.svg");
						display: inline-block;
						width: 200px;
					}
					&.group-link-heading {
						cursor: default;
						font-size: 15px;
						font-weight: 600;
						pointer-events: none;
					}
				}
				img {
					&.social-icon {
						width: 47px;
						padding: 0px;
					}
				}
				li {
					padding-bottom: 10px;
				}
			}
			.legal {
				font-size: 12px;
				text-align: right;
				li {
					margin-left: 10px;
				}
			}
		}
	}


@media all and (max-width: 1230px) and (min-width: 769px) {

		#contact-us-section {
			padding: 10px 10%;
			.col-sm-12 {
				padding: 7px 0px;
			}
		}
		#contact-form-section {
			.row {
				margin-bottom: 0px;
				padding-bottom: 0px;
				.title {
					margin-bottom: 10px;
				}
				input, select, textarea, button {
					margin: 14px 0px;
				}
			}
		}
		#links-section {
			padding: 20px 10%;
			.links {
				margin-bottom: 0px;
				a {
					&.logo {
						margin-bottom: 15px;
					}
					&.group-link-heading {
						margin-top: 15px;
					}
				}
			}
		}
	}


@media all and (max-width: 768px) {

		#contact-us-section {
			padding: 0px 35px;
			.col-sm-12 {
				border-bottom: 1px solid $footer-divider;
				padding: 15px 0px;
				ul, li {
					margin: 0px;
					padding: 0px;
				}
			}
			.col-sm-12:last-child {
				border: 0px;
				#toggle-contact-form-chevron {
					float: right;
					margin: 5px auto;
				}
			}
		}
		#contact-form-section {
			padding: 20px 35px 0px 35px;
			.row , .row:nth-last-child(3), .row:nth-last-child(2), .row:last-child{
				margin: 0px;
				padding: 0px;
				.col-sm-12 {
					padding: 0px 20px;
					margin: 0px 0px 20px 0px;
				}
			}
		}
		#links-section {
			padding: 20px 50px;
			.links {
				margin-bottom: inherit;
				a {
					&.logo:after {
						content: url("/images/logo-ampersand-white.svg");
						display: inline-block;
						width: 30px;
					}
				}
				b {
					font-weight: 500;
				}
				.col-sm-12 {
					border-bottom: 1px solid $footer-divider;
					padding: 15px 0px;
					ul, li {
						margin: 0px;
						padding: 0px;
					}
				}
				.col-sm-12:last-child {
					border: 0px;
				}
			}
			.legal {
				text-align: center;
				.col-sm-12 {
					float: none;
					margin: 0 auto;
					ul {
						font-size: 10px;
						li:first-child {
							clear: both;
							display: block;
						}
						li:nth-child(3) {
							border-left: 1px solid $footer-divider;
							border-right: 1px solid $footer-divider;
							padding: 0px 16px;
						}
					}
				}
			}
		}
	}



